/*general format*/	
@font-face {
    font-family: forte;
    src: url(assets/font/FORTE.TTF);
}
@font-face {
    font-family: open sans;
    src: url(assets/font/OpenSans-Regular.ttf);
}
@font-face {
    font-family: open sans light;
    src: url(assets/font/OpenSans-Light.ttf);
}
*{
	font-family:"open sans",Verdana,sans-serif;
}
html{
	min-height:100vh;
	position:relative;
}
h1,h2,h3{
	font-weight:500;
}
body{
	margin:0px;
	padding:0px;
}
.hidden {
	display:none !important;
}
p{
	margin:0px 10px;
	display: inline;
}
button{
  	border: 1px solid #777;
  	display: inline;
}

/****header****/
header{
	background-color:rgba(0,0,0,0.9);
	box-shadow:0px -9px 10px 6px #bf49ff;
	position:fixed;
	width:100%;
	max-height:60px;
}
header a{
	color:inherit;
	text-decoration:inherit;
	font-family:inherit;
}
header img{
	height:60px;
	margin:0px 0px 0px 100px;
	float:right;
}
header h1{
	color:white;
	font-family:forte;
	font-size:40px;
	display:inline;
	margin: 0px 0px 0px 50px;
}
/***footer**/
footer{
	background-color:#f3f6f8;
	text-align:center;
	bottom:0px;
	width:100%;
	position:absolute;
}
footer i{
	font-size:40px !important;
	margin:0px 20px;
}
footer p{
	margin:0px;
}
footer a{
	color:inherit;
	text-decoration:inherit;
}
.bottom{
	background-color:black;
	color:white;
	padding:10px 80px;
}
.bottom p{
	display:inline;
}

/*********index*********/
.purple{
	color:#bf49ff;
	font-weight:bold;
}
.pink{
	color:#f37fff;
	font-weight:bold;
}
.main{
	margin:auto;
	width:70%;
	padding:60px 40px 10px 40px;
}
.widgets{
	list-style-type:none;
	padding:0px;
}
.widgets li{
	height: 50px;
	margin:0px;
	background-color:#fef8ff;
}
.widgets li+li{
	border-top:1px solid #fbdfff;
}
.widgets a{
	font-size:20px;
	padding:10px;
	margin:auto;
	display: inline-block;
}